@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{background:url(/static/media/ball-1.84fb8676.png) no-repeat center center fixed;background-size:cover;overflow-y:auto;overflow-x:hidden}body,#root{height:100%;width:100%;margin:0;padding:0}.app{text-align:center;color:#e0e0e0;font-family:"Roboto",sans-serif;overflow:hidden;position:relative}.app *{box-sizing:border-box}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}::-webkit-scrollbar{width:10px}::-webkit-scrollbar-track{background:#f1f1f1}::-webkit-scrollbar-thumb{background:#f88a62}::-webkit-scrollbar-thumb:hover{background:#f87c4f}
